.city_list {
  .city_list_view {
    border-top: 1px solid #ccc;
    background-color: #fff;
    position: relative;
    .city_group_title {
      height: 40px;
      display: flex;
      align-items: center;
      padding-left: 15px;
    }
    .city_group_list {
      .city_item {
        padding-left: 15px;
        height: 40px;
        display: flex;
        align-items: center;
        border-bottom: 1px solid #eee;
      }
    }

    .letter_list {
      padding-top: 9px; 
      position: absolute;
      top:0;
      right: 9px;
 
      display: flex;
      flex-direction: column;
      height: 100%;
      .letter_item {
        flex:1;
        text-align: center;
        span{
          border-radius: 50%;
     
          // display: inline-block;
          display: block; 
          width: 20px;
          height: 20px;
          display: flex;
          justify-content: center;
          align-items: center;
          font-size: 12px;
   
        }
      }
      .active{
        span{
          background-color: #21b97a;
          color:#fff;
        }
      }
    }
  }
}
