/* 轮播图 */
.index_carousel{
  position: relative;
  .city_search_wrap{
    position: absolute;
    width: 90vw;
    top:10px;
    left: 10vw;
    z-index: 100;
  }
}
/* 导航 */
.index_nav {
  display: flex;
  background-color: #fff;
  .nav_item {
    flex: 1;
    padding: 10px 0px;
    text-align: center;
    .nav_title {
      padding: 5px 0;
    }
    img {
      width: 70%;
    }
  }
}

/* 租房小组 */
.rent_group {
  .rent_group_title {
    height: 45px;
    padding: 0 16px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    span:nth-child(1) {
      color:#000;
      font-weight: 600;
      font-size: 16px;
    }
  }

  .rent_group_content {
    padding: 10px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .rent_item {
      height: 90px;
      background-color: #fff;
      width: 49%;
      margin-bottom: 10px;
      display: flex;
      padding: 0 10px;
      align-items: center;
      .rent_item_info {
        flex:1;
        .rent_title {
          color:#000;
          font-size: 15px;
        }
        .rent_desc {
          font-size: 13px;
        }
      }

      .rent_item_img {
       flex:1;
       text-align: right;
        img {
          width: 80%;
        }
      }
    }
  }
}
