.filter {
  background-color: #fff;
  .filter_main {
    z-index: 1001;
    position: relative;
    background-color: #fff;
    .filter_title {
      display: flex;
      height: 40px;
      align-items: center;
      justify-content: space-around;
      border-bottom: 1px solid #eee;
      .title_item {
        font-size: 14px;
      }
      .active {
        color: #21b97a;
      }
    }

    .filter_content {
      /* 1到3 筛选组件 */
      .main_pickerview {
        .picker_btn {
          border-top: 1px solid #ccc;
          border-bottom: 1px solid #ccc;
          height: 45px;
          display: flex;
          span {
            display: flex;
            justify-content: center;
            align-items: center;
          }
          span:nth-child(1) {
            color: #21b97a;
            flex: 1;
          }
          span:nth-child(2) {
            flex: 3;
            background-color: #21b97a;
            color: #fff;
          }
        }
      }
      /* 第四个筛选组件 */
      .other_content {
        display: flex;
        position: fixed;
        width: 100vw;
        height: 100vh;
        top: 0;
        left: 0;
        z-index: 1000;
        .other_mask {
          flex: 1;
          background-color: #000;
          opacity: 0.5;
        }

        .other_main {
          flex: 5;
          display: flex;
          flex-direction: column;
          background-color: #fff;
          .other_main_top {
            flex: 1;
            overflow: auto;
            padding: 15px;
            .other_group {
              border-bottom: 1px solid #ccc;
              &:last-child {
                border-bottom: none;
              }
              .other_group_title {
                font-size: 16px;
                display: flex;
                height: 50px;
                align-items: center;
              }

              .other_group_content {
                display: flex;
                flex-wrap: wrap;
                .other_item {
                  width: 75px;
                  height: 35px;
                  border: 1px solid #ccc;
                  border-radius: 5px;
                  display: flex;
                  align-items: center;
                  justify-content: center;
                  margin-right: 18px;
                  margin-bottom: 18px;
                }
                .active{
                  background-color: #defaef;
                  color:#21b97a;
                  border-color: #21b97a;
                }
              }
            }
          }

          .other_main_btn {
            height: 50px;
            display: flex;
            border-top: 1px solid #ccc;
            span {
              display: flex;
              align-items: center;
              justify-content: center;
            }
            span:nth-child(1) {
              color: #21b97a;
              flex: 1;
            }
            span:nth-child(2) {
              background-color: #21b97a;
              color: #fff;
              font-size: 16px;
              flex: 2;
            }
          }
        }
      }
    }
  }
  .filter_mask{
    position: fixed;
    width: 100vw;
    height: 100vh;
    top:0;
    left: 0;
    z-index: 1000;
    background-color: #000;
    opacity: .5;
  }
}
