.map_found_view {
  position: relative;
  .map_view {
    height: calc(100vh - 45px);
    .house_label {
      background-color: #0cb56ae6;
      color: #fff;
      border: 1px solid #fff;
    }
    .circle {
      border-radius: 50%;
      width: 80px;
      height: 80px;
      flex-direction: column;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    .rect {
      padding: 5px;
    }
  }
  .house_detail {
    position: absolute;
    width: 100vw;
    height: 60vh;
    bottom: 0;
    left: 0;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    .house_detail_title {
      height: 40px;
      padding: 0 10px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      background-color: #ccc;
      span:nth-child(1) {
        font-size: 16px;
        color: #000;
      }
    }

    .house_detail_list {
      flex:1;
      overflow: auto;

    }
  }
}
