.city_Search {
  height: 35px;
  display: flex;
  .city_Search_form {
    flex: 1;
    background-color: #fff;
    display: flex;
    padding: 10px 3px;
    border-radius: 5px;
    .city_label {
      padding: 0 10px;
      border-right: 1px solid #000;
    }

    .city_input {
      flex: 1;
      display: flex;
      align-items: center;
      :global{
        .icon-seach{
         margin: 0 15px;
        }
      }
    }
  }

  .city_Search_icon {
    width: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
   

    /* 使用全局样式 */
    :global {
      .icon-map{
        font-size: 20px;
        color:#fff;
     
      }
    }
  }
}
